import {adaptObject} from './base'

// 默认模板
var _tipsTpl='<div class="ui-poptips ui-poptips-<%=type%>">'+
    '<div class="ui-poptips-cnt <%=extraclass%>">'+
    '<%=content%>'+
    '</div>'+
    '</div>';

// 默认参数
var defaults={
    content:'',
    stayTime:1000,
    type:'info',
    extraclass: '',     // 自定义样式
    callback:function(){}
}
// 构造函数
var Tips = function (el,option,isFromTpl) {
    var self=this;
    this.element=$(el);
    this._isFromTpl=isFromTpl;
    this.elementHeight=$(el).height();

    this.option=$.extend(defaults,option);
    $(el).css({
        "-webkit-transform":"translateY(-"+this.elementHeight+"px)"
    });
    setTimeout(function(){
        $(el).css({
            "-webkit-transition":"all .5s"
        });
        self.show();
    },20);

}
Tips.prototype={
    show:function(){
        var self=this;
        // self.option.callback("show");
        self.element.trigger($.Event("tips:show"));
        this.element.css({
            "-webkit-transform":"translateY(0px)"
        });
        if(self.option.stayTime>0){
            setTimeout(function(){
                self.hide();
            },self.option.stayTime)
        }
    },
    hide :function () {
        var self=this;
        self.element.trigger($.Event("tips:hide"));
        this.element.css({
            "-webkit-transform":"translateY(-"+this.elementHeight+"px)"
        });
        setTimeout(function(){
            self._isFromTpl&&self.element.remove();
        },500)


    }
};
const tip = (option) => {
    defaults.extraclass = ''
    return adaptObject(this, defaults, option,_tipsTpl,Tips,"tips");
};
const closeTip = (tip) => {
    let $tip = $(tip);
    $tip.css({
        "-webkit-transform":"translateY(-"+$tip.height()+"px)"
    });
    setTimeout(function(){
        $tip.length&&$tip.remove();
    },500)
};

export {tip,closeTip}


